import React, { memo } from "react";
import { Tag, Divider, Descriptions, List, Avatar } from "antd";
import { BlogBarWrap } from "./style";

export default memo(function BlogBar(props) {
  const data = [
    {
      title: "nvm:nodenvm",
    },
    {
      title: "CentOS7nodejs(npm",
    },
    {
      title: "NodeJS Event Loop ",
    },
    {
      title: "js nodejs",
    },
  ];
  return (
    <BlogBarWrap>
        <div>
          <Descriptions title="About" column={{ xs: 1, sm: 1, md: 1, lg: 2 }}>
            <Descriptions.Item label="">10</Descriptions.Item>
            <Descriptions.Item label="">9</Descriptions.Item>
            <Descriptions.Item label="">JavaScript</Descriptions.Item>
            <Descriptions.Item label="">empty</Descriptions.Item>
          </Descriptions>
        </div>
        <Divider orientation="left"></Divider>
        <div>
          <Tag color="green">NodeJs</Tag>
          <Tag color="blue"></Tag>
          <Tag color="geekblue">JavaScript</Tag>
        </div>
        <br />
        <div className="ant-descriptions-title"></div>
        <List
          itemLayout="horizontal"
          dataSource={data}
          renderItem={(item) => (
            <List.Item>
              <List.Item.Meta
                avatar={
                  <Avatar src="https://api.gaojianhua.top/api/v1/img/8dd3e5d1ea095b5c514fd39a77c06567/image/jpeg" />
                }
                title={<a href="#/Blog">{item.title}</a>}
                description="Note that the development build is not optimized.
                To create a production build, use yarn build."
              />
            </List.Item>
          )}
        />
    </BlogBarWrap>
  );
});
